<template>
<div>
<notifications class="alert-info"   group="foo" />
  <table class="table margin" :class="tableClass">
    <thead>
    <slot name="columns">
      <th class="capitalize" v-for="column in columns" :key="column">{{column}} <img class="filter-th" src="@/assets/havr-icon/filterTableIcon.svg"></th>
    </slot>
    </thead>
    <tbody>
    <tr v-for="(item, index) in data.slice(store.state.keychain.startPaginationItem - 1, store.state.keychain.endPaginationItem)" :key="index">
      <slot :row="item">
        <td style="cursor: pointer;" @click='setDetailsItem(item)' class="margin-td capitalize" v-for="(column, index) in columns"
            :key="index"
      >
          <div  class="container-table"><div class="valid" v-if="column === 'status' && item[column] === 'active'"><i class="ti-check light"></i></div>
          <div class="valid" v-if="column === 'status' && item[column] === 'inactive'"><i class="ti-close inactive"></i></div>
           {{item[column]}}</div>
        </td>
      </slot>
    </tr>
    </tbody>
  </table>
</div>
</template>
<script>
import store from "@/flulux/store";
import lodash from "lodash";
import { keychainRepository } from "@/repository/keychain";
import { userDataRepository } from "@/repository/users";
import userRepository from "@/repository/users";
import repository from "@/repository/keychain";
export default {
  name: "paper-table",
  data() {
    return {
      store: store,
      userDataRepository: userDataRepository,
      notificationToReturn: []
    };
  },
  props: {
    columns: Array,
    data: Array,
    type: {
      type: String, // striped | hover
      default: "striped"
    },
    maxRow: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    }
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  methods: {
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    setDetailsItem(p) {
      this.notificationToReturn = []
      this.store.state.keychain.keylog = [];
      let obj = {};
      if (p.shared) {
        obj = lodash.filter(keychainRepository.shared_key, {
          door_lock_id: p.id
        });
      } else {
        obj = lodash.filter(keychainRepository.door_locks, { id: p.id });
      }
      if(p.access === 'Normal (non admin)') {
        return this.$notify({
          group: "foo",
          title: "Details lock door is not available with " + p.access + " status",
          classes: "vue-notification error"
        });
      }
      this.store.actions.OPEN_DETAILS_DRAWER(true);
      this.store.actions.SET_DETAILS_KEYCHAIN(obj[0]).then(res => {
        repository.getLogs(obj[0].id).then(res => {
          if (keychainRepository.actvityKey.length === 0)
            return this.store.actions.SET_ACTIVITY(false);
          this.getLogs().then(res =>
            this.store.actions.SET_ACTIVITY(this.notificationToReturn)
          );
        });
      });
    },
    async getLogs() {
          const res = await keychainRepository.actvityKey.map(async (i) => {
          const resp = await this.buildLogs(i)
          await this.notificationToReturn.push(resp)
        })
        return res
    },
    async buildLogs(i) {
      let res = await userRepository.retrieveUser(i.user_id)
          const picture = lodash.filter(userDataRepository.contactPictures, {
            id: i.user_id
          });
          if (picture.length !== 0) {
            i.picture = picture[0].img;
          } else {
              const res = await userRepository.getContactPicture(i.user_id)
              i.picture = res.base64;
          }
          i.fullName = res.first_name + " " + res.last_name;
          return i
  }
  }
};
</script>
<style>
.margin-td {
  padding-bottom: 20px !important;
  padding-top: 20px !important;
}

.table > tbody > tr {
  position: relative;
  border-bottom: 2px solid #e6e6e6;
}

.capitalize {
  text-transform: capitalize;
}
.grey {
  color: #e6e6e6;
}
.valid {
  width: 14px;
  height: 14px;
  margin-right: 10px;
  border-radius: 10px;
  background-color: #c6f363;
  position: relative;
  top: 2px;
}
.container-table {
  display: flex;
}
.light {
  color: white;
}
.margin {
  position: relative;
  top: 8em;
}
.filter-th {
  margin-left: 25px;
}

.inactive {
  background: #e74c3c;
  border-radius: 10px;
  color: white;
}
</style>
