
import { ENDPOINTS, API_BASE_URL } from '../constant.js'
import { dataRepository } from '@/repository/login'
import loginRepository from './login'
export default {
    /**
    * login wrapper to get access token
    * @param {access_token} p 
    */
    async getDoorLocks(p) {
        const res = await loginRepository.retrieveToken()
        const lockDoor = await this.retrieveLockDoor(res)
    },

    async getSharedK(p) {
        const res = await loginRepository.retrieveToken()
        const sharedKey = await this.retrieveSharedKeys(res)

    },

    async retrieveSharedKeys(token) {
        const resp = await fetch(API_BASE_URL + ENDPOINTS.shared_keys, {
            method: "GET",
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + token }
        })
        if (resp.status === 200) {
            let parsedResp = await resp.json()
            console.log(parsedResp, 'shared')
            return keychainRepository.shared_key = [...parsedResp]

        }
    },

    /**
     * get all doorlock of a user
     * @param {user token} token 
     */
    async retrieveLockDoor(token) {
        const resp = await fetch(API_BASE_URL + ENDPOINTS.keychain, {
            method: "GET",
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + token }
        })
        if (resp.status === 200) {
            const res = await resp.json()
            return keychainRepository.door_locks = [...res]

        }
        else {
            return location.href = '/#/login'
        }

    },


    async getLogs(p) {
        const res = await loginRepository.retrieveToken()
        const logs = await this.retrieveLockDoorLogs(p, res)

    },

    /**
     * get log of key
     */
    async retrieveLockDoorLogs(p, token) {
        const res = await fetch(API_BASE_URL + ENDPOINTS.doorlocklog, {
            method: 'post',
            headers: { "Content-Type": "application/json", 'Authorization': 'Bearer: ' + token },
            body: JSON.stringify({ door_lock: { door_lock_id: parseInt(p) } })
        })
        let logs = await res.json()
        logs.sort((a,b) =>{
            return new Date(b.date) - new Date(a.date);
          });
        return keychainRepository.actvityKey = logs
    },
}

export let keychainRepository = {
    door_locks: [],
    shared_key: [],
    actvityKey: []
}